@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
/* font style will go here. */

* {
    font-family: 'Roboto', sans-serif; 
}

body {
    width: 100%;
    margin: 0 auto;
}
div.App {
    background-color: #E1DFEB;
   
}


section.header {
    position: relative;
    height: 300px;
    overflow: hidden;
}

  
div.headerBG {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background-image: linear-gradient(#2fc4ff, #2647ff); */
    background-image: linear-gradient(#4e4d57, #E1DFEB);
    transform: skewY(-6deg);
    transform-origin: top left;
}

section.header h1,
section.header h2,
section.header h3
 {
    position: relative;
    margin: 0;
    text-align: center;
}

section.header h1 {
    padding-top: 80px;
    font-size: 44px;
}


